$background_color: #eee;

body {
  background-color: $background_color;
  font-family: Arial, Helvetica, sans-serif;
}

.title-container {
  text-align: center;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.cell {
  border: black 2px solid;
  background-color: $background_color;
  flex: 0 0 32%;
  height: 150px;
  display: flex;
  justify-content: center;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: darken($background_color, 8%);
  }

  &.disabled {
    background-color: $background_color;
  }
}

.cell-0-0 {
  border-left: none;
  border-top: none;
}

.cell-0-1 {
  border-top: none;
}

.cell-0-2 {
  border-top: none;
  border-right: none;
}

.cell-1-0 {
  border-left: none;
}

.cell-1-2 {
  border-right: none;
}

.cell-2-0 {
  border-left: none;
  border-bottom: none;
}

.cell-2-1 {
  border-bottom: none;
}
.cell-2-2 {
  border-right: none;
  border-bottom: none;
}

.player1 {
  background: black;
  height: 120px;
  position: relative;
  width: 20px;
  transform: rotate(45deg);
  &:after {
    background: black;
    content: '';
    height: 20px;
    left: -50px;
    position: absolute;
    top: 50px;
    width: 120px;
  }
}

.player2 {
  border: 20px solid black;
  border-radius: 50%;
  height: 80px;
  width: 80px;
}
